.console {
    font-family: Consolas,Monaco,Lucida Console,Liberation Mono,DejaVu Sans Mono,Bitstream Vera Sans Mono,Courier New, monospace;
  }

  .typewriter  {
    overflow: hidden; 
    border-right: .15em solid green; 
    white-space: nowrap; 
    margin: 0 ; 
    letter-spacing: .10em; 
    -webkit-animation: 
      typing 3.5s steps(40, end),
      blink-caret .75s step-end infinite; 
            animation: 
      typing 3.5s steps(40, end),
      blink-caret .75s step-end infinite;
  }
  
  @-webkit-keyframes typing {
    from { width: 0 }
    to { width: 100% }
  }
  
  @keyframes typing {
    from { width: 0 }
    to { width: 100% }
  }
  
  @-webkit-keyframes blink-caret {
    from, to { border-color: transparent }
    50% { border-color: green; }
  }
  
  @keyframes blink-caret {
    from, to { border-color: transparent }
    50% { border-color: green; }
  }
.slider {
    position: relative;
    height: 60vh;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .image {
    width: 1000px;
    height: 600px;
    border-radius: 30px;
  }
  
  .right-arrow{
    position: absolute;
    top: 45%;
    right: 0pc;
    font-size: 6rem;
    color: black;
    z-index: 10;
    cursor: pointer;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
  }
  
  .left-arrow {
    position: absolute;
    top: 45%;
    left: 0pc;
    font-size: 6rem;
    color: black;
    z-index: 10;
    cursor: pointer;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
  }
  
  .slide {
    opacity: 0;
    transition-duration: 1.5s ease;
  }
  
  .slide.active {
    opacity: 1;
    transition-duration: 1.4s;
    -webkit-transform: scale(1.08);
            transform: scale(1.08);
  }
.PlayButton{
    font-size: 6rem;
    border: none;
    background-color: transparent;
    background: linear-gradient(to right, white 50%, black 50%);
    background-size: 200% 100%;
    background-position: right bottom;
    transition: all .5s ease-out;

}

